import uniSection from '@/components/uni-section/uni-section.vue'
import uniPopup from '@/components/uni-popup/uni-popup.vue'
import uniIcons from '@/components/uni-icons/uni-icons.vue'
import uniFav from '@/components/uni-fav/uni-fav.vue'
import uniNavBar from '../../../components/uni-nav-bar/uni-nav-bar.vue'
export default {
	components: {
		uniSection,
		uniPopup,
		uniIcons,
		uniFav,
		uniNavBar,
	},
    data() {
            return {
				PageCur: '我的',
				CustomBar: this.CustomBar,
				modalName: null,
				modalNameS: null,
				modalNameS1: null,
				type: '',
				content: '顶部弹 popup',
				checkList: [false, false, false, false, false, false],
				listData1: [
					{
						description1: ' 芜湖社区c栋3楼楼灯损坏2个，希望及时处理，已经造成老人摔倒事故多起，希望能得到及时解决，谢谢。',		
						images0: '../../../static/input/形状9@3x.png',
						description2: '催办',
					},
				],
            }
        },
    methods: {
    	NavChange: function(e) {
    		this.PageCur = e.currentTarget.dataset.cur
    	},
    	goMore(url) {
    		uni.navigateTo({
    			url: url
    		});
    	},
    	showModal(e) {
    		this.modalName = e.currentTarget.dataset.target
    	},
    	hideModal(e) {
    		this.modalName = null
    	},
    	showModalS(e) {
    		this.modalNameS = e.currentTarget.dataset.target
    	},
    	hideModalS(e) {
    		this.modalNameS = null
    	},
		showModalS1(e) {
			this.modalNameS1 = e.currentTarget.dataset.target
		},
		hideModalS1(e) {
			this.modalNameS1 = null
		},
    	RadioChange(e) {
    		this.radio = e.detail.value
    	},
		DelImg1(e) {
			uni.showModal({
				title: '提示信息',
				content: '确定要删除该条记录吗？',
				cancelText: '取消',
				confirmText: '删除',
				success: res => {
					if (res.confirm) {
						this.listData1.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
    	ChooseCheckbox(e) {
    		let items = this.checkbox;
    		let values = e.currentTarget.dataset.value;
    		for (let i = 0, lenI = items.length; i < lenI; ++i) {
    			if (items[i].value == values) {
    				items[i].checked = !items[i].checked;
    				break
    			}
    		}
    	},
    	open(){
    	         this.$refs.popup.open()
    	      },
    	favClick(index) {
    		this.checkList[index] = !this.checkList[index]
    		this.$forceUpdate()
    	},
    	togglePopup(type, open) {
    		switch (type) {
    			case 'top':
    				this.content = '顶部弹出 popup'
    				break
    	
    			case 'bottom':
    				this.content = '底部弹出 popup'
    				break
    			case 'center':
    				this.content = '居中弹出 popup'
    				break
    		}
    		this.type = type
    		this.$nextTick(() => {
    			this.$refs['show' + open].open()
    		})
    	},
    }
}
